import {get, post} from '@/utils/request'

// 查询所有用户
export async function friendAllSearch(model) {
    return await post('friend/all/search', model)
}
// 添加查询到的用户
export async function friendAdd(model) {
    return await post('friend/add', model)
}
// 同意好友
export async function friendAgree(model) {
    return await post('friend/agree', model)
}
// 拒绝好友
export async function friendReject(model) {
    return await post('friend/reject', model)
}