<template>
  <div>
    <div class="box">
      <div class="box_top">
        <van-search
          placeholder="请输入手机号"
          type="number"
          autofocus
          maxlength="11"
          v-model="keyword"
          shape="round"
          class="search_box"
        />
        <van-button
          round
          type="primary"
          size="small"
          class="search_btn"
          :disabled="loading"
          @click="searchbtn"
        >搜索</van-button>
      </div>
      <div class="history">
        <van-cell-group v-if="userInfo.sendFriend && userInfo.sendFriend.length > 0">
          <van-cell v-for="(item, index) in userInfo.sendFriend" :key="index">
            <template slot="title">
                <img :src="imgbox[item.headimg].src" class="imgbg" />
                <div class="imgcot">
                  <div>{{item.name}}</div>
                  <div>({{item.tel}})</div>
                </div>
            </template>
            <template slot="default">
              <span class="custom-title left mid" v-if="item.status === 1">已申请</span>
              <span class="custom-title left mid" v-else-if="item.status === 2">已同意</span>
              <span class="custom-title left mid" v-else-if="item.status === 3">已拒绝</span>
            </template>
          </van-cell>
        </van-cell-group>
        <div v-if="userInfo.sendFriend && userInfo.sendFriend.length > 0" class="clearHis" @click="clear">清空历史记录</div>
      </div>
      <div class="box_mid">
        <div slot="content">
          <van-cell-group v-if="list && list.length > 0">
            <van-cell v-for="(item, index) in list" :key="index">
              <template slot="title">
                <img :src="imgbox[item.headimg].src" class="imgbg" />
                <div class="imgcot">
                  <div>{{item.name}}</div>
                  <div>({{item.tel}})</div>
                </div>
              </template>
              <template slot="default">
                <span class="custom-title left mid" v-if="item.tel != userInfo.tel">
                  <van-button round type="info" size="mini" @click="handelAgree(item)">添加</van-button>
                </span>
              </template>
            </van-cell>
          </van-cell-group>
        </div>
      </div>
      <!-- <van-loading v-if="loading" type="spinner" color="#1989fa" class="loading" /> -->
      <load :loading="loading"></load>
    </div>
  </div>
</template>

<script>
import load from "@/components/loading/loading";
import { validateTel } from "@/utils/validator";
import { Toast } from "vant";
import { friendAllSearch, friendAdd } from "@/api/lianxiren";
import { mapState, mapMutations } from "vuex";
export default {
  name: "sousuo",
  components: {
    load
  },
  data() {
    return {
      keyword: "",
      loading: false,
      list: [],
      imgbox: [
        { id: 0, src: require("../../../assets/fourth/manf.png") },
        { id: 1, src: require("../../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../../assets/fourth/mans.png") },
        { id: 3, src: require("../../../assets/fourth/womans.png") }
      ]
    };
  },
  computed: {
    ...mapState(["userInfo"])
  },
  methods: {
    ...mapMutations(["updateUserInfo"]),
    clear() {
      this.userInfo.sendFriend = []
      this.updateUserInfo(this.userInfo)
      this.$socket.emit('clearSendFir',this.userInfo.tel)
    },
    async searchbtn() {
      if (validateTel(this.keyword)) {
        this.loading = true;
        let model = {
          tel: this.keyword
        };
        let res = await friendAllSearch(model);
        this.list = []
        if (res) {
          if (res.type === 0) {
            Toast("查询出错");
          } else if (res.type === 1) {
            this.list.push(res.data);
          } else if (res.type === 3) {
            Toast("该手机号未注册");
          }
        } else {
          Toast("查询出错");
        }
      } else {
        Toast("请输入正确的手机号码");
      }
      setTimeout(() => {
        this.loading = false;
      }, 500);
      this.keyword = "";
    },
    async handelAgree(item) {
      let model = {
        tel: this.userInfo.tel, //自己电话
        histel: item.tel, //对方的电话
        hisheadimg: item.headimg, //对方的头像
        headimg: this.userInfo.headimg, //自己的头像
        name: this.userInfo.name,
        hisname: item.name,
        datamine: JSON.stringify(this.userInfo), //自己数据
      };
      let res = await friendAdd(model);
      if (res) {
        if (res.type === 0) {
          Toast("出错");
        } else if (res.type === 1) {
          Toast("你已经发送了申请了");
        } else if (res.type === 2) {
          Toast("发送成功");
          this.updateUserInfo(res.data);
          this.$socket.emit('addFriend', model)
        } else if (res.type === 3) {
          Toast("你们已经是好友");
        }
      } else {
        Toast("出错");
      }
      this.list = []
    }
  }
};
</script>
<style lang="scss" scoped>
.content_app {
  height: calc(100vh - 46px) !important;
}
.scroll-list-wrap {
  height: calc(100vh - 100px) !important;
}
.box_top {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .search_box {
    flex: 11;
  }
  .search_btn {
    flex: 1;
  }
}
.imgbg {
  width:40px;
  height:40px;
  float: left;
}
.imgcot {
  float: left;
  margin-top: -4px;
  margin-left: 5px
}
.mid {
  line-height: 44px;
}
.history .van-cell .van-cell__title {
  flex: 2 !important
}
.box_mid .van-cell .van-cell__title {
  flex: 2 !important
}
.box_mid {
  position: relative;
  height: auto;
}
.clearHis {
  height:30px;
  font-size: 14px;
  color: lightgray;
  text-align: center;
  line-height: 30px;
}
.box {
  position: relative;
  height: calc(100vh - 46px);
  // .loading {
  //   position: absolute;
  //   left: 50%;
  //   bottom: 50%;
  //   z-index: 2;
  // }
}
</style>